.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    height: auto;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

.img{
    height: 70px;
    width: 70px;
    margin-left: 6%;
    margin-top: 15px;
    margin-bottom: 0px;
    position: absolute;
     border: 3px solid rgba(62, 69, 81, 0.3);
     border-radius: 50px;
  }













@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}